import React from 'react'
import { joinStringsWithDot } from '../../../util'

import styles from '../Common.module.css'

type Props = {
    poss: LinkedInPosition[]
}

const Experience = (props: Props) => {
    const { poss } = props
    return (
        <div className={styles.root} id="experience">
            <div className={`card ${styles.cardBody}`}>
                <div className="card__header margin--auto">
                    <h3 style={{ fontSize: 36, marginTop: 8, marginBottom: 0 }}>Experience</h3>
                </div>
                <div style={{ textAlign: `center` }}>
                    {poss.length ?
                        poss.map((pos: LinkedInPosition, i) => {
                            const { description, companyName, companyLogo, title, dateRange, location, url } = pos
                            return (

                                <div key={i} className={`card__body ${styles.innerCardBody}`}>
                                    <hr style={{ marginTop: 12 }} />
                                    {
                                        companyLogo &&
                                        <div className={`avatar avatar--vertical ${styles.avatarMobileLogo} margin--auto`}>
                                            {url ?
                                                <a href={url} target="_blank" rel="noopener noreferrer" >
                                                    <img className="hoverLarge" src={companyLogo} alt={companyName} />
                                                </a>
                                                :
                                                <img src={companyLogo} alt={companyName} />
                                            }
                                        </div>
                                        /// TODO:- no logo case
                                    }
                                    <div className="avatar">
                                        {
                                            companyLogo &&
                                            <div className={`avatar ${styles.avatarLogo}`}>
                                                {url ?
                                                    <a href={url} target="_blank" rel="noopener noreferrer"  style={{ width: '80%', height: '80%', margin: 'auto' }} >
                                                        <img className="hoverLarge" src={companyLogo} alt={companyName}/>
                                                    </a>
                                                    :
                                                    <img src={companyLogo} alt={companyName} style={{ width: '80%', height: '80%', margin: 'auto' }} />
                                                }
                                            </div>
                                        }
                                        <div className={`avatar__intro ${styles.avatarContent}`}>
                                            <h4 className="avatar__name" style={{ fontSize: 20, marginBottom: 4, textAlign: `left` }}>{title}</h4>
                                            <div className={styles.centerOnMobile}>
                                                {
                                                    joinStringsWithDot([
                                                        `${companyName ? companyName : ``}`,
                                                        dateRange,
                                                        location ?? ``
                                                    ])
                                                }
                                            </div>
                                            <div className={styles.jobContent}>
                                                {description?.map((d, i) => <p key={i}>{d}</p>)}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                        :

                        <div className={`card__body ${styles.innerCardBody}`}>
                            <hr style={{ marginTop: 12 }} />
                            <h3 style={{ margin: `auto` }}>Add some positions and it'll appear here!</h3>
                        </div>
                    }
                </div>
            </div>
        </div>
    )
}

export default Experience