import React from 'react'
import styles from '../Common.module.css'


type Prop = {
    projs: LinkedInProject[]
}

const IndivCard = (props: { proj: LinkedInProject }) => {
    const { proj } = props
    return (

        <div className="col col--4" style={{ marginBottom: `1.5rem` }}>

            <div className="card" style={{ backgroundColor: `var(--ifm-color-gray-800)`, height: `100%` }}>
                <div className="card__header">
                    <h3>{proj.title}</h3>
                </div>
                <div className="card__body" style={{ textAlign: "justify" }}>
                    {proj.description && proj.description.map((d, i) => <p key={i}>{d}</p>)}
                </div>
                {
                    proj.url &&
                    <div className="card__footer">
                        <a href={proj.url} target="_blank" rel="noopener noreferrer" >
                            <button className="button button--secondary button--block">See Project</button>
                        </a>
                    </div>
                }
            </div>
        </div>
    )
}

const Projects = (props: Prop) => {
    const { projs } = props


    return (
        <div className={styles.root} id="projects">
            <div className={`card ${styles.cardBody}`}>
                <div className="card__header margin--auto">
                    <h3 style={{ fontSize: 36, marginTop: 8, marginBottom: 0 }}>Projects</h3>
                </div>
                <div className={`card__body ${styles.innerCardBody}`}>
                    <hr style={{ marginTop: 12 }} />
                    <div style={{ margin: 'auto', textAlign: `center` }} className="row">
                        {projs.length
                            ?
                            projs.map((proj, i) => <IndivCard key={i} proj={proj} />)
                            :
                            <h3 style={{ margin: `auto` }}>Add some projects and it'll appear here!</h3>}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Projects