import React from 'react'

import styles from '../Common.module.css'
import { joinStringsWithDot } from '../../../util'


type Props = {
    edus: LinkedInEducation[]
}

const Education = (props: Props) => {
    const { edus } = props

    return (
        <div className={styles.root} id="education">
            <div className={`card ${styles.cardBody}`}>
                <div className="card__header margin--auto">
                    <h3 style={{ fontSize: 36, marginTop: 8, marginBottom: 0 }}>Education</h3>
                </div>
                <div style={{ textAlign: `center` }}>
                    {edus.length ?
                        edus.map((edu, i) => {
                            const { degreeName, activities, fieldOfStudy, grade, schoolName, schoolLogo, notes, yearFrom, yearTo, url } = edu
                            const startYear = yearFrom
                            const endYear = yearTo
                            return (

                                <div key={i} className={`card__body ${styles.innerCardBody}`}>
                                    <hr style={{ marginTop: 12 }} />
                                    {
                                        schoolLogo &&
                                        <div className={`avatar avatar--vertical ${styles.avatarMobileLogo} margin--auto`}>
                                            {url ?
                                                <a href={url} target="_blank" rel="noopener noreferrer" >
                                                    <img className="hoverLarge" src={schoolLogo} alt={schoolName} />
                                                </a>
                                                :
                                                <img src={schoolLogo} alt={schoolLogo} />
                                            }</div>
                                    }
                                    <div className="avatar">
                                        {
                                            schoolLogo &&
                                            <div className={`avatar ${styles.avatarLogo}`}>
                                                {url ?
                                                    <a href={url} target="_blank" rel="noopener noreferrer" style={{ width: '80%', height: '80%', margin: 'auto' }} >
                                                        <img className="hoverLarge" src={schoolLogo} alt={schoolName} />
                                                    </a>
                                                    :
                                                    <img src={schoolLogo} alt={schoolName} style={{ width: '80%', height: '80%', margin: 'auto' }} />
                                                } </div>
                                        }
                                        <div className={`avatar__intro ${styles.avatarContent}`} style={{ textAlign: `left` }}>
                                            <h4 className="avatar__name" style={{ fontSize: 20, marginBottom: 4 }}>{schoolName}</h4>
                                            <div className={styles.centerOnMobile}>
                                                {
                                                    joinStringsWithDot(
                                                        [
                                                            `${degreeName} ${fieldOfStudy}`,
                                                            `${startYear} - ${endYear}`
                                                        ]

                                                    )
                                                }

                                            </div>
                                            {grade && <p>Grade: {grade}</p>}
                                            <div className={styles.jobContent}>
                                                {notes && <h4 style={{ marginBottom: 2, marginTop: 8 }}>Description</h4>}
                                                {notes?.map((d, i) => <p key={i}>{d}</p>)}
                                            </div>
                                            <div className={styles.jobContent}>
                                                {activities && <h4 style={{ marginBottom: 2, marginTop: 8 }}>Activities</h4>}
                                                {activities?.map((d, i) => <p key={i}>{d}</p>)}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                        :

                        <div className={`card__body ${styles.innerCardBody}`}>
                            <hr style={{ marginTop: 12 }} />
                            <h3 style={{ margin: `auto` }}>Add education histories and it'll appear here!</h3>
                        </div>}
                </div>
            </div>
        </div>
    )
}

export default Education