import React, { useState, useEffect } from 'react';
import './App.css';

// import 'bootstrap/dist/css/bootstrap.min.css';
import '../node_modules/infima/dist/css/default-dark/default-dark.css';
import Header from './containers/Header/Header';
import Footer from './containers/Footer/Footer';
import Content from './containers/Content/Content';

const App = () => {
  const [profile, setProfile] = useState<LinkedInProfile | null>(null)
  const [error, setError] = useState(false)

  useEffect(() => {
    const localStoredProfile = window.localStorage.getItem(`linkedInProfile`)
    if (localStoredProfile) {
      console.log(`got from localStorage`)
      const got: LinkedInProfile = JSON.parse(localStoredProfile)
      setProfile(got)
    }
    else {
      console.log(`fetching from json`)
      /// fetchit
      fetch(`${process.env.PUBLIC_URL}/linkedInProfile.json`)
        .then(response => response.json())
        .then(data => { setProfile(data) })
        .catch((err) => { console.error(err); setError(true) })
    }
  }, [])

  if (error) {
    return (
      <h3 style={{ textAlign: `center`, marginTop: `45vh`, fontSize: 50 }}>Oops... something went wrong!</h3>
    )
  }

  return (
    <div className="App">
      {profile ?
        <div>
          <Header fullName={profile.fullName} />
          <Content profile={profile} />
          <Footer contacts={profile.contacts} />
        </div> : <h3 style={{ textAlign: `center`, marginTop: `45vh`, fontSize: 50 }}>Loading...</h3>
      }
    </div>
  );
}

export default App;
