import React from 'react'

import styles from './Content.module.css'
import Experience from './Experience/Experience'
import Education from './Education/Education'
import Skills from './Skills/Skills'
import Projects from './Projects/Projects'
import { joinStringsWithDot } from '../../util'
import defaultbanner from '../../assets/defaultbanner.png'

type Prop = {
    profile: LinkedInProfile;
}


const Hero = (props: { profile: LinkedInProfile }) => {
    const { profile } = props
    return (
        <div className={styles.heroRoot}>
            <img
                className={styles.backgroundImg}
                src={profile.backgroundPicUrl ? profile.backgroundPicUrl : defaultbanner}
                alt="Background"
            />
            <div className={styles.heroInnerContent}>
                <div className="avatar avatar--vertical">
                    <img
                        className={[styles.profilePic, `avatar__photo`].join(` `)}
                        src={profile.profilePicUrl}
                        alt="Profile"
                    />
                    <div className="avatar__intro margin-check">
                        <h4 className="avatar__name">{profile.fullName}</h4>
                        <small>
                            {profile.headline}
                        </small>
                        <small>
                            {joinStringsWithDot([profile.currentCompany ?? ``, profile.currentSchool ?? ``])}
                        </small>
                        <small>
                            {profile.location}
                        </small>

                    </div>
                </div>
            </div>
        </div>

    )
}

const QuoteSection = (props: { quote: string[] }) => {
    const { quote } = props
    if (quote.length) {
        return (
            <div style={{ marginTop: -8 }}
                className={`${styles.announcement}, ${styles.announcementDark}`}>
                <div className={styles.announcementInner}>
                    {quote.map((q, i) => <p key={i}>{q}</p>)}
                </div>
            </div>
        )
    }
    else return <span />
}



const Content = (props: Prop) => {
    const { profile } = props
    return (<div className={styles.root} id="top">
        <Hero profile={profile} />
        <QuoteSection quote={profile.summary ?? []} />
        <Experience poss={profile.positions} />
        <Education edus={profile.educations} />
        <Skills skills={profile.skills} />
        <Projects projs={profile.projects} />
    </div>)
}

export default Content